
import React from 'react';    
import {Form} from 'subschema';


    
const schema = {
  "schema": {
    "title": {
      "type": "Select",
      "options": [
        "Mr",
        "Mrs",
        "Ms"
      ]
    },
    "name": {
      "type": "Text",
      "validators": [
        "required"
      ]
    },
    "age": {
      "type": "Number"
    }
  },
  "fieldsets": [
    {
      "legend": "Name",
      "fields": "title, name, age",
      "buttons": [
        {
          "label": "Cancel",
          "action": "cancel",
          "type": "reset",
          "buttonClass": "btn"
        },
        {
          "label": "Submit",
          "action": "submit",
          "buttonClass": "btn btn-primary"
        }
      ]
    }
  ]
};
    









export default class App extends React.Component {
    render(){
        return <div>
            <h3>Anewworld</h3>
            <p></p>
           <Form schema={schema}/>
          </div>
    }
}


